.header{
    display: flex;
    width: 100%;
    background-color: rgb(253, 253, 253);
    text-align: center;
    margin-bottom: 50px;
    position: fixed;
    z-index: 9999;
    border-bottom: 1px rgb(233, 233, 233) solid;
}
.logo{
    display: inline-block;
    right: 100px;
    font-size: 2rem;
    font-weight: bold;
    margin-right: 40%;
}

.listitems{
    display: flex;
    justify-content: center;
    overflow: hidden;
    margin: 0;
    font-weight: bold;
    width: 100%;
}


li{
    display: flex;
    list-style-type: none; 
    padding: 15px;
}

li a{
    text-decoration: none;
    padding: 10px;
    color:#24303a;
    transition: all .3s ease-in;
}
li a:hover{
    color: darkslategray;
    transform: scale(1.1);
}

.icon, .icona {
    display: none;
    cursor: pointer;
    margin: 5px;
    padding: 5px;
    transition: .3s;
}



@media screen and (max-width: 1250px) {
    .header{
        width: 100%;
    }
    .listitems li{
        display: none;
        
    }

    li{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        padding: 3px;
    } 

    .logo{
        display: block;
        text-align: center;
        margin: 0;
    }
    

    .mobileview{
        display: flex;
        flex-direction: column;
        border-bottom: rgb(45, 53, 53) 3px solid;
        
    }
    .icon, .icona{
        display: block; 
        justify-content: center;
    }
}

