.container{
    display: block;
    background-color: #252f31;
    width: 100%;
    height: 40vh;
    padding-top: 70px;
}

.containerWrap{
    display: flex;
    align-items:center;
    flex-direction: column;
    width: 100%;
    background-color: #252f31;
    overflow: hidden;
}

.container h1{
    text-align: center;
    color: #fff;
}

.container .info .email{
    font-size: 1.5rem;
    font-weight: bold;
    color: aliceblue;
}

.container .info p{
    margin: 20px;
    color: rgb(228, 226, 226);
    font-size: 1.5em;
    
}
.icons{
    display: flex;
}
.icon{
    font-size: 30px ;
    padding: 10px;
    color: #fff;
}
.icon:hover{
    color: rgb(212, 203, 203);
    transition: all .3s ease-in;
    cursor: pointer;
    filter: invert(50%);
}

@media (max-width: 1250px){
    .container{
        width: 100%;
        padding: 0;
    }
    .container .info .emailtxt{
        font-size: 20px;
    }
    .container .info .emailto {
        display: block;
        margin-top: 15px;
        font-size: .7em;
    }
    .container .info .resdownload{
        margin-bottom: 20px;
        width: 130px;
    }
}